<template>
	<div class="project-create">
		<el-form ref="form" :model="createform" label-width="120px">
		  	<el-form-item label="">
		    	<el-input v-model="createform.projectName"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-select v-model="createform.parentProjectName" placeholder="">
		      		<el-option
				      	v-for="item in parentProjectNameOptions"
				      	:key="item.value"
				      	:label="item.label"
				      	:value="item.value">
				    </el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.machineNo"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-select v-model="createform.importanceLevel" placeholder="">
		      		<el-option
				      	v-for="item in importantLever"
				      	:key="item.value"
				      	:label="item.label"
				      	:value="item.value">
				    </el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.powerRate"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.rotateSpeed"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.oilConsumeSign"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.torsionSpace"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.speedPointTotal"></el-input>
		  	</el-form-item>

		  	<el-form-item label="">
		    	<el-input v-model="createform.speedPointTotalLimit"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.parentProjectName"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-input v-model="createform.speedPointTotalHight"></el-input>
		  	</el-form-item>
		  	<el-form-item label="MAP">
		    	<el-select v-model="createform.mapPicture" placeholder="">
		      		<el-option label="1" value="1"></el-option>
		      		<el-option label="2" value="2"></el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="ECU">
		    	<el-input v-model="createform.ECU"></el-input>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-select v-model="createform.standard" placeholder="">
		      		<el-option label="" value="1"></el-option>
		      		<el-option label="" value="2"></el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="EGR">
		    	<el-select v-model="createform.egr" placeholder="">
		      		<el-option label="" value="1"></el-option>
		      		<el-option label="" value="2"></el-option>
		      		<el-option label="" value="2"></el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-select v-model="createform.fuelSystem" placeholder="">
		      		<el-option label="" value="1"></el-option>
		      		<el-option label="" value="2"></el-option>
		      		<el-option label="CB18" value="3"></el-option>
		      		<el-option label="CB28" value="4"></el-option>
		    	</el-select>
		  	</el-form-item>

		  	<el-form-item label="">
		    	<el-select v-model="createform.intake" placeholder="">
		      		<el-option label="" value="1"></el-option>
		      		<el-option label="" value="2"></el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-select v-model="createform.midleCooling" placeholder="">
		      		<el-option label="" value="1"></el-option>
		      		<el-option label="" value="2"></el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-select v-model="createform.valueNo" placeholder="">
		      		<el-option label="2" value="1"></el-option>
		      		<el-option label="4" value="2"></el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item label="">
		    	<el-select v-model="createform.stoke" placeholder="">
		      		<el-option label="4*100x127" value="1"></el-option>
		      		<el-option label="6*100x127" value="2"></el-option>
		      		<el-option label="6*105x130" value="2"></el-option>
		    	</el-select>
		  	</el-form-item>
		  	<el-form-item>
		    	<el-button type="primary" @click="confirmCreateProject"></el-button>
		   		<el-button @click="cancelCreateProject"></el-button>
		  	</el-form-item>
		</el-form>
	</div>
</template>
<script>
	
	import { createProject } from  'api/project';

	export default {
    data() {
      return {
        createform: {
  			projectName: '',
			parentProjectName: 222,
			projectContent: 222,
			projectPrincipal: '',   //
			machineType: '4d160',
			machineNo:'hc503222',
			powerRate:160.5, // 
			rotateSpeed: 2200,
			oilConsumeSign: 230.1,
			torsionSpace: 800,
			speedPointTotal:1600,
			importanceLevel:1,  //1, 
			planBeginTime:'2017-1-1',
			planEndTime:'2017-1-2',
			dais:2,  //  
			status:1,  // 1  
			isStoppage: 1,
			memo:'wangbadan'
        },
        parentProjectNameOptions: [
        	{
	          	value: '1',
	          	label: '1'
	        },
	        {
	          	value: '1',
	          	label: '2'
	        },
	        {
	          	value: '3',
	          	label: '3'
	        },
        ],
        importantLever: [
        	{
	          	value: '1',
	          	label: ''
	        },
	        {
	          	value: '1',
	          	label: ''
	        }
        ]
      }
    },
    methods: {
      confirmCreateProject() {
      	let params = this.createform;
        createProject(this, params)
        	.then(res => {
        		if(res.status == 0) {
        			this.$message({
                        message: '',
                        type: 'success',
                        duration: 1000,
                    });
                    setTimeout(()=>{
	                    this.$router.push({
			                path: '/frame/project',
			            })
                    })
        		}
        	})
      },
      cancelCreateProject() {
      		this.$router.push({
                path: '/frame/project',
                query: {
                    
                }
            })
      }
    }
  }
</script>
<style lang="scss">
	.project-create {
		max-width: 800px;
	}
</style>


