<template>
	<div class="project">

	<!--  -->
        <div class="ai_search">
           	<el-form :inline="true" :model="searchForm"  >
				  	<el-form-item label="">
				    	<el-input size="small" v-model="searchForm.projectName"></el-input>
				  	</el-form-item>

				  	<el-form-item label="">
				    	<el-select size="small" v-model="searchForm.parentProjectName" placeholder="">
				      		<el-option
						      	v-for="item in parentProjectNameOptions"
						      	:key="item.value"
						      	:label="item.label"
						      	:value="item.value">
						    </el-option>
				    	</el-select>
				  	</el-form-item>

				  	<el-form-item label="">
				    	<el-input size="small" v-model="searchForm.projectPrincipal"></el-input>
				  	</el-form-item>

				  	<el-form-item label="">
				    	<el-input size="small" v-model="searchForm.machineType"></el-input>
				  	</el-form-item>

				  	<el-form-item label="">
				    	<el-input size="small" v-model="searchForm.machineNo"></el-input>
				  	</el-form-item>

				  	<el-form-item label="">
				    	<el-select size="small" v-model="searchForm.importanceLevel" placeholder="">
				      		<el-option
						      	v-for="item in projectSataus"
						      	:key="item.value"
						      	:label="item.label"
						      	:value="item.value">
						    </el-option>
				    	</el-select>
				  	</el-form-item>
				  	<el-form-item label="">
					  	<el-date-picker
					  		size="small"
						    v-model="value7"
						    type="daterange"
						    align="right"
						    unlink-panels
						    range-separator=""
						    start-placeholder=""
						    end-placeholder=""
						    :picker-options="pickerOptions2">
	   					 </el-date-picker>
   					</el-form-item>
           	</el-form> 
        </div>

        <div class="">
			<el-button type="primary" @click="searchProject"></el-button>
		</div>

        <div class="">
			<el-button type="primary" @click="cteateProject"></el-button>
		</div>

	<!--  -->
	<el-dialog title="" :visible.sync="backProjectDialog">
		  	<el-form :model="backForm" label-width="80px">
			    <el-form-item label="">
				    <el-input type="textarea" v-model="backForm.memo"></el-input>
				</el-form-item>
		  	</el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="backProjectDialog = false"> </el-button>
		    <el-button type="primary" @click="ConfirmBackProject"> </el-button>
		  </div>
		</el-dialog>

	<!--  -->
		<el-table
    ref="multipleTable"
    :data="projectlist"
    tooltip-effect="dark"
    style="width: 100%"
    >
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
      	label=""
      	prop="projectName"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="parentProjectName"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="projectPrincipal"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="machineType"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="machineNoList"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="importanceLevel"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="planBeginTime"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="planEndTime"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="createTime"
      	>
    </el-table-column>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="dais"
      	>
    </el-table-column>
    <el-table-column
      	label=""
      	prop="status"
      	>
    </el-table-column>
    </el-table-column>
    <el-table-column
      fixed="right"
      label=""
      width="180">
      <template slot-scope="scope">
        <el-button  type="text" size="small" @click="goToDetail(scope.row)"></el-button>
        <el-button  type="text" size="small" @click="_setProjectPass(scope.row, 'pass')"></el-button>
        <el-button  type="text" size="small" @click="_setProjectBack(scope.row, 'back')"></el-button>
      </template>
    </el-table-column>
  </el-table>
	</div>
</template>

<script>
	import { getProjectList, setProjectPass, setProjectBack } from 'api/project';
	export default {
		data() {
			return {
				backForm: {
					memo: ''
				},
				backProjectDialog: false,
				projectlist:[
					
				],
				searchForm: {
					projectName: '6d',
					parentProjectName: '',
					projectPrincipal: '',   //
					machineType: '4d160',
					machineNo:'hc503222',
					powerRate:160.5, // 
					rotateSpeed: 2200,
					oilConsumeSign: 230.1,
					torsionSpace: 800,
					speedPointTotal:1600,
					importanceLevel:1,  //1, 
					planBeginTime:'2017-1-1',
					planEndTime:'2017-1-2',
					projectCycle:'7',
					operationInstruction:'http://repucljlsj.com/path',
					propertyCharacterList:'http://repucljlsj.com/path',
					machineNoList:'http://repucljlsj.com/path',
					dais:2,  //  
					status:1,  // 1  
					isStoppage: 1,  
					createTime:2017-1-1,
					updateTime:2017-1-1,
					memo:'wangbadan'
				},

				pickerOptions2: {
          			shortcuts: [
          				{
				            text: '',
				            onClick(picker) {
				              	const end = new Date();
				              	const start = new Date();
				              	start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
				              	picker.$emit('pick', [start, end]);
				            }
          				},
          				{
				            text: '',
				            onClick(picker) {
				            const end = new Date();
				            const start = new Date();
				            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
				            picker.$emit('pick', [start, end]);
				        }
          			}, {
			            text: '',
			            onClick(picker) {
			              const end = new Date();
			              const start = new Date();
			              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
			              picker.$emit('pick', [start, end]);
			            }
          			}
          			]
        		},

        		parentProjectNameOptions: [
	        	{
		          	value: '1',
		          	label: '1'
		        },
		        {
		          	value: '1',
		          	label: '2'
		        },
		        {
		          	value: '3',
		          	label: '3'
		        },
	        ],

	        importantLever: [
	        	{
		          	value: '1',
		          	label: ''
		        },
		        {
		          	value: '1',
		          	label: ''
		        }
        	],
        	projectSataus: [
        		{
		          	value: '1',
		          	label: ''
		        },
		        {
		          	value: '2',
		          	label: ''
		        },
		        {
		          	value: '3',
		          	label: ''
		        },
		        {
		          	value: '4',
		          	label: ''
		        },
		        {
		          	value: '5',
		          	label: ''
		        },
		        {
		          	value: '6',
		          	label: ''
		        }
        	],

        		value6: '',
        		value7:'',
			}
		},
		created() {
			this._getProjectList();
			let backid = '';
		},
		methods: {

			ConfirmBackProject() {
				this.backProjectDialog = false;
				let params = {
					"id": this.backid,
					"memo" : this.backForm.memo
				}
					setProjectBack(this, params)
						.then(res => {
							if(res.status == 0) {
								this.$message({
			                        message: '',
			                        type: 'success',
			                        duration: 1000,
			                    });
			                    setTimeout(()=>{
			                    	window.location.reload();
			                    },1000)
							} else {
								this.$message({
			                        message: '',
			                        type: 'error',
			                        duration: 1000,
			                    });
							}
						})

			},
			// 
			_setProjectPass(data) {
				console.log(data);
				let id = data.id;
				let params = {
					"id": id
				}
				setProjectPass(this,params)
					.then(res => {
						console.log(res)
					})
			},
			_setProjectBack(data) {
				this.backProjectDialog = true;
				this.backid = data.id
			},

			// 
			goToDetail(data) {
				alert('')
			},
			// 
			cteateProject(){
				this.$router.push({
                    path: '/frame/createproject',
                    query: {
                        
                    }
                })
			},
			// 
			_getProjectList() {
				getProjectList(this)
					.then(res => {
						if(res.status == 0) {
							this.projectlist = res.data;
						}
					})
			},

			searchProject() {

			}
		}
	}
</script>


<style lang="scss">

</style>


